<template>
    <v-container>
      <v-card>
        <v-card-title>{{ post.title }}</v-card-title>
        <v-card-subtitle>作者: {{ post.userId.username }}</v-card-subtitle>
        <v-card-text>{{ post.content }}</v-card-text>
        <v-btn @click="likePost">点赞 ({{ post.likes }})</v-btn>
      </v-card>
      <CommentList :postId="post._id" />
    </v-container>
  </template>
  
  <script>
  import axios from 'axios';
  import CommentList from './CommentList.vue';
  
  export default {
    data() {
      return {
        post: {},
      };
    },
    components: {
      CommentList,
    },
    async created() {
      const response = await axios.get(`http://localhost:3000/api/posts/${this.$route.params.id}`);
      this.post = response.data.post;
    },
    methods: {
      async likePost() {
        const token = localStorage.getItem('token');
        await axios.post(`http://localhost:3000/api/likes/like/${this.post._id}`, {}, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        this.post.likes++;
      },
    },
  };
  </script>
  
  <style>
  /* 添加样式 */
  </style>