<template>
    <v-container>
      <v-form @submit.prevent="createComment">
        <v-text-field v-model="content" label="评论内容" required></v-text-field>
        <v-btn type="submit" color="success">提交评论</v-btn>
      </v-form>
    </v-container>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    props: ['postId'],
    data() {
      return {
        content: '',
      };
    },
    methods: {
      async createComment() {
        const token = localStorage.getItem('token');
        try {
          await axios.post(`http://localhost:3000/api/comments/create/${this.postId}`, {
            content: this.content,
          }, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });
          this.$emit('comment-added');
          this.content = '';
        } catch (error) {
          alert(error.response.data.message);
        }
      },
    },
  };
  </script>
  
  <style>
  /* 添加样式 */
  </style>